<template>
  <div class="hello">
      <p class="page-title">当前位置:角色管理</p>
        <div class="search">
            <Input v-model="userName" placeholder="角色名" style="width: 150px" />
            <Button type="primary">搜索</Button>
            <Button type="primary" @click="addUser">添加角色</Button>
        </div>
         <Table border :columns="columns" :data="data"></Table>
           <div class="pages"v-if="Total>10">
                <Page :total="Total" @on-change="pagesc" />
         </div>

       <Modal
        v-model="modal1"
       width="340"
        title="添加角色"
        @on-ok="ok"
        >
         <Input v-model="value" placeholder="角色名称" style="width: 300px" />
    </Modal>
   
  </div>
</template>
<script>
var self = null;
 export default {
        data () {
            return {
                value:"",Total:0,
                modal1:false,
                userName:"",
                columns: [
                    {
                        title: '角色名称',
                        key: 'title',
                      
                    },
                    {
                        title: '状态',
                        key: 'status',
                          render: (h, params) => {
                            return h('div', [
                                h('span', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                       
                                    }
                                }, params.row.status==1?'开启':'关闭'),
                               
                            ]);
                        }
                    },
                     
                     {
                        title: '创建时间',
                        key: 'create_time',
                          render: (h, params) => {
                            return h('div', [
                                h('span', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                       
                                    }
                                },new Date(params.row.create_time).toLocaleDateString()),
                               
                            ]);
                        }

                    },
                    {
                        title: 'Action',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('span', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px',
                                        cursor:"pointer"
                                    },
                                    on: {
                                        click: () => {
                                            var data ={
                                                    id:params.row.id,
                                                    status:params.row.status==2?1:2
                                                }
                                                this.$api.upjuese({
                                                        data:data,
                                                        success:res=>{
                                                            self.$Message.info('操作成功');
                                                            self.pageinfo();
                                                          
                                                        }
                                                })
                                        }
                                    }
                                }, params.row.status==2?'开启':'关闭'),
                               
                            ]);
                        }
                    }
                ],
                data: [ ]
            }
        },
        mounted(){
            self = this;
            this.pageinfo();
        },
        methods: {
            pageinfo(){
                var that = this;
                this.$api.getuserlist({
                    data:{
                        type:2 , 
                        current_page:this.page},
                    success:res=>{
                        console.log(res.data)
                            that.data = res.data.list;
                            that.Total = res.data.Total;

                    }
                })
            },
             pagesc(d){
                    this.page=d
                     this.pageinfo();

            },
            ok(){
                 var data={
                     title:this.value,
                      type:2  
                 }   
                    this.$api.addjuese({
                        data:data,
                        success:res=>{
            this.pageinfo();

                                console.log(res)
                        }
                    })
                 

            },
            addUser(){
                    this.modal1 = true


            },
            show (index) {
                this.$Modal.info({
                    title: 'User Info',
                    content: `Name：${this.data6[index].name}<br>Age：${this.data6[index].age}<br>Address：${this.data6[index].address}`
                })
            },
            remove (index) {
                this.data6.splice(index, 1);
            }
        }
    }
</script>



